* {
  margin: 0;
  padding: 0;
}

.none {
  display: none !important;
}

.blockEvents {
  pointer-events: none !important;
}

.overflowHidden {
  overflow: hidden !important;
}

body {
  max-width: 1600px;
  color: #fff;
  background: #f0fafa;
  grid-template-columns: 1fr 1fr;
  margin: auto;
  font-family: Montserrat, sans-serif;
  display: grid;
}

header {
  grid-area: 1 / 1 / 2 / 3;
}

header img {
  max-width: 100%;
  height: auto;
}

nav {
  background: #ddd;
  grid-area: 2 / 1 / 3 / 3;
