}

.M-main {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  justify-content: center;
  display: grid;
}

.M-main a {
  text-align: center;
  min-width: 70px;
  height: 20px;
  color: #fff;
  background: -webkit-linear-gradient(top, #3be, #09c);
  border: 3px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin: 2px;
  padding: 5px;
  font-size: 15px;
  transition: all .1s;
  display: block;
}

.M-main a:hover {
  color: #066;
  background: -webkit-linear-gradient(top, #5df, #3be);
  font-weight: bold;
}

.M-main a:active {
  font-weight: bold;
  box-shadow: inset 1px 1px 10px #000;
}

nav [type="checkbox"], .TM {
  display: none;
}

.TM {
  color: #fff;
