@media (max-width: 960px) {
  body {
    grid-template-columns: 1fr;
    padding: 1px;
    display: grid;
  }

  header {
    grid-area: 1 / 1 / 2 / 2;
  }

  nav {
    min-width: 100%;
    max-width: 100%;
    background: #eee;
    grid-area: 2 / 1 / 3 / 2;
    padding-left: 1px;
    overflow: hidden;
  }

  .Li1:hover .Ul2 {
    opacity: 1;
    top: 0;
  }

  .TM {
    display: block;
  }

  .M-main {
    display: none;
  }

  .M-main a {
    height: 20px;
    text-align: left;
    padding: 10px;
    font-size: 16px;
  }

  input.TM:checked ~ ul {
    max-height: 5000px;
    display: block;
    position: relative;
  }

  .product-selection {
    height: auto;
    grid-area: 4 / 1 / 5 / 2;
  }

  .product-selection form {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .product-selection-1 {
    grid-area: 1 / 1 / 2 / 2;
  }

  .product-selection-2 {
    grid-area: 1 / 2 / 2 / 3;
  }

  .information {
    grid-area: 5 / 1 / 6 / 2;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    display: grid;
  }

  footer {
    grid-area: 6 / 1 / 7 / 2;
  }
}

@media (max-width: 720px) {
  .product-selection {
    grid-area: 4 / 1 / 5 / 2;
  }

  .product-selection form {
    grid-template-columns: 1fr;
    display: grid;
  }

  .product-selection-1 {
    grid-area: 1 / 1 / 2 / 2;
  }

  .product-selection-2 {
    grid-area: 2 / 1 / 5 / 2;
  }

  .information {
    grid-area: 5 / 1 / 6 / 2;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    display: grid;
  }

  footer {
    grid-area: 6 / 1 / 7 / 2;
    overflow-x: scroll;
  }
}

